.hero {
	position: relative;
	height: 755px;
	padding: 70px 0;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	@include md {
		height: 100vh; }
	&__title {
		position: absolute;
		left: 50px;
		bottom: 45px;
		z-index: 1;
		@include sm {
			left: 15px; } }
	&__wrapper {
		position: absolute;
		left: 0;
		bottom: 0;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		width: 100%;
		height: 76%;
		text-align: center;
		color: $title3;
		text-shadow: 3px 3px 4px rgba(0,0,0,.66);
		@include sm {
			height: 60%;
			font-size: 7vw; } }
	&__promo {
		margin-top: 80px;
		padding: 20px;
		background-color: rgba(0,0,0,0.4);
		font-size: 80px;
		@include sm {
			font-size: 12vw;
			margin-top: 25px; } } }

.logo {
	display: block;
	width: 442px;
	float: left;
	// height: 109px
	img {
		display: block;
		max-width: 100%;
		max-height: 100%; }
	@include sm {
		@include css-lock(290, 442, 320, 767, width); } }

.countdown-timer {
	float: right;
	@include md {
		float: none;
		margin-top: 50px;
		display: inline-block;
		width: 100%; }

	ul > li {
		float: left;
		background: linear-gradient(0deg,#121313,#2e3135 100%,#272727 100%);
		color: #fff;
		height: 124px;
		width: 130px;
		margin-right: 15px;
		position: relative;
		@include md {
			width: 30%;
			margin-right: 5%;
			height: auto; }


		&:last-child {
			margin-right: 0; }

		.smalltext {
			font-size: 30px;
			position: absolute;
			top: -44px;
			width: 100%;
			text-align: center;
			text-transform: uppercase;
			@include sm {
				font-size: 5vw;
				top: -22px; } }

		span {
			@include textB;
			font-size: 110px;
			display: block;
			line-height: 124px;
			text-align: center;
			text-shadow: 6px 5px 5px rgba(0,0,0,0.7);
			@include sm {
				line-height: 70px;
				font-size: 18vw; } } } }

.options {
	padding: 60px 0;
	background-color: #080807;
	@include md {
		padding: 50px 0; }
	&__items {
		margin: -25px;
		@include justify;
		@include r(1250) {
			display: flex;
			justify-content: space-around;
			flex-wrap: wrap;
			&:after {
				display: none; } }
		@include sm {
			margin: -15px; }
		@include xs {
			margin: -15px -10px; } }
	&__item {
		display: inline-block;
		vertical-align: top;
		padding: 25px;
		text-align: center;
		@include r(1250) {
			width: 33.3333%; }
		@include sm {
			width: 50%;
			padding: 15px; }
		@include xs {
			padding: 15px 10px; } }
	&__ic {
		height: 100px;
		margin-bottom: 25px;
		@include vertical;
		img {
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; }
		@include sm {
			height: 75px; } }
	&__text {
		font-size: 24px;
		line-height: 1.25;
		color: $grey;
		@include sm {
			font-size: 20px; }
		@include xs {
			font-size: 18px; } } }

.product {
	$H: 680px;
	position: relative;
	overflow: hidden;
	background-color: #fff;
	@include r(1250) {
		padding: 30px 0; }
	&__items {
		padding-right: 30px;
		margin: 0 -30px;
		font-size: 0;
		@include r(1250) {
			margin: 0 -20px; }
		@include md {
			padding-right: 20px; }
		@include sm {
			padding-bottom: 10px;
			padding-right: 10px;
			margin: 0 -10px; } }
	&__item {
		display: inline-block;
		height: $H;
		padding: 0 30px;
		@include vertical;
		&_l {
			width: 380/16.6+%;
			padding: 30px; }
		&_r {
			width: 630/16.6+%;
			padding: 30px; }
		&_m {
			width: 650/16.6+%; }
		img {
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; }
		@include r(1250) {
			height: auto;
			padding: 0 20px; }
		@include md {
			padding: 0 10px; } }
	&__btn {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 175px;
		z-index: 1;
		text-align: center;
		@include md {
			top: 0;
			bottom: 0;
			@include vertical;
			.btn-img {
				vertical-align: middle; } }
		@include sm {
			position: static; } } }

.banner {
	position: relative;
	overflow: hidden;
	height: 780px;
	padding: 70px 0;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	@include r(1250) {
		height: 100vh; }
	@include sm {
		padding: 50px 0; }
	&__in {
		padding: 0 20px; }
	&__list {
		position: absolute;
		left: 70px;
		bottom: 60px;
		@include sm {
			left: 35px;
			bottom: 50px; } }
	&__link {
		$shadow: 0px 4px 0px rgba(#000, 0.66);
		position: relative;
		display: block;
		padding-left: 25px;
		text-shadow: $shadow;
		@include textSB;
		font-size: 48px;
		line-height: 1.2;
		color: #fff;
		&_h {
			@include textB;
			font-size: 62px; }
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			z-index: 1;
			width: 8px;
			height: 9px;
			margin: auto;
			box-shadow: $shadow;
			background-color: #fff; }
		&_h:after {
			display: none; }
		@include sm {
			@include css-lock(48*.6666, 48, 320, 767);
			&_h {
				@include css-lock(62*.6666, 62, 320, 767); } } }
	&__review {
		img {
			width: 100%; } } }

.about {
	$L: (610/16+%);
	&__in {
		position: relative;
		padding: 50px 0 60px $L;
		@include md {
			padding: 50px 0; } }
	&__img {
		position: absolute;
		top: -45px;
		left: 0;
		bottom: 0;
		z-index: 1;
		width: $L;
		font-size: 0;
		max-height: 760px;
		img {
			position: absolute;
			top: 0;
			right: 0;
			z-index: 1;
			width: auto;
			height: 100%; }
		@include md {
			display: none; } }
	&__dsc {
		max-width: 790px;
		padding-left: 10px;
		@include md {
			max-width: none;
			padding-left: 0; } }
	&__title {
		margin-bottom: 10px; }
	&__subtitle {
		padding-right: 120px;
		text-align: right;
		margin-bottom: 30px;
		@include r(1250) {
			padding-right: 0px; }
		@include sm {
			text-align: left; } }
	&__dt {
		display: inline-block;
		margin-bottom: 10px;
		border-bottom: 4px solid $outline;
		@include textSB;
		font-size: 36px;
		line-height: 1.1;
		color: $dark;
		text-transform: uppercase;
		@include sm {
			@include css-lock(24, 36, 320, 767); } }
	&__dd {
		margin-bottom: 35px;
		font-size: 20px;
		line-height: 1.2;
		color: $text;
		&:last-child {
			margin-bottom: 0; }
		@include sm {
			font-size: 18px; } } }

.steps {
	$P: &;
	padding: 160px 0;
	background-color: $dark;
	color: #fff;
	@include r(1250) {
		padding: 100px 0; }
	@include md {
		padding: 80px 0; }
	&__items {
		margin: 0 -25px;
		font-size: 0;
		text-align: center;
		@include md {
			@include clr;
			margin: 0; } }
	&__item {
		max-width: 33.3333%;
		padding: 0 25px;
		display: inline-block;
		vertical-align: top;
		color: #fff;
		@include md {
			width: auto;
			max-width: none;
			padding: 0;
			float: left;
			clear: left;
			text-align: left;
			margin-bottom: 40px;
			&:last-child {
				margin-bottom: 0; } }
		@include xs {
			float: none;
			display: block;
			text-align: center; } }
	&__ic {
		height: 210px;
		margin-bottom: 80px;
		@include vertical;
		img {
			vertical-align: middle;
			max-width: 100%;
			max-height: 100%; }
		@include r(1250) {
			margin-bottom: 50px; }
		@include md {
			width: 100px;
			height: 100px;
			display: inline-block;
			vertical-align: middle;
			margin-bottom: 0;
			margin-right: 20px; }
		@include xs {
			margin-right: 0;
			margin-bottom: 30px; } }
	&__name {
		display: block;
		@include textSB;
		font-size: 36px;
		line-height: 1.4;
		text-transform: uppercase;
		white-space: nowrap;
		span {
			border-bottom: 3px solid transparent;
			@include tr(.2s); }
		#{$P}__item:hover & span {
			border-color: #fff; }
		@include r(1250) {
			font-size: 32px;
			white-space: normal; }
		@include md {
			display: inline-block;
			vertical-align: middle;
			font-size: 24px;
			span {
				border-width: 2px; } }
		@include xs {
			display: block; } } }

.feedback {
	padding: 60px 0 115px;
	text-align: center;
	@include md {
		padding: 50px 0 60px; }
	&__title {
		margin-bottom: 25px; }
	&__subtitle {
		margin-bottom: 20px; } }

.fb-form {
	display: table;
	width: 840px;
	margin: 0 auto;
	@include md {
		width: 100%;
 }		// box-shadow: 0 0 0 2px khaki
	@include xs {
		display: block; }
	&__cell {
		display: table-cell;
		vertical-align: top;
		&_input {
			width: 100%; }
		@include xs {
			display: block;
			text-align: left; } }
	&__el {
		padding: 15px;
		@include textSB;
		font-size: 36px;
		color: $dark;
		text-transform: uppercase;
		text-align: center;
		@include tr(.2s);
		@include md {
			padding: 10px 15px;
			font-size: 32px; } }
	&__el_field {
		width: 100%;
		border-bottom: 1px solid #cececf;
		background-color: #fff;
		color: $dark;
		@include placeholder {
			color: $dark;
			@include tr(.2s); }
		&:focus {
			@include placeholder {
				opacity: 0; } }
		@include xs {
			margin-bottom: 20px; } }
	&__el_btn {
		width: 170px;
		background-color: $dark;
		color: #fff;
		&:active {
			transform: scale(.9); }
		.no-touch &:hover {
			background-color: lighten($dark, 5%); }
		@include xs {
			width: 100%; } } }

.footer {
	// padding: 65px 0 360px
	padding: 65px 0;
	background-color: #f7f7f8;
	@include md {
		padding: 50px 0 25px; }
	&__in {
		max-width: 630px;
		margin: 0 auto; }
	&__soc {
		margin-bottom: 95px;
		@include md {
			margin-bottom: 50px; } }
	&__copy {
		@include justify;
		span {
			display: inline-block;
			vertical-align: middle;
			@include textM;
			font-size: 24px;
			line-height: 1.2;
			color: #080807; }
		@include sm {
			span {
				@include css-lock(12, 24, 320, 767); } }
		@include r(320) {
			span {
				font-size: 12px; } } } }

.social {
	margin: -20px;
	@include justify;
	@include md {
		margin: -15px; }
	&__el {
		display: inline-block;
		vertical-align: middle;
		padding: 20px;
		@include md {
			padding: 15px; } }
	&__el a {
		display: block;
		height: 125px;
		@include vertical;
		@include md {
			height: 75px; }
		@include sm {
			height: 50px; } }
	&__el img {
		vertical-align: middle;
		max-width: 100%;
		max-height: 100%; } }

.popup {
	$P: &;
	position: relative;
	position: fixed;
	top: 50px;
	left: 0;
	right: 0;
	bottom: 50px;
	z-index: 1000;
	margin: auto;
	max-width: 960px;
	margin: auto;
	background-color: #fff;
	font-size: 0;
	opacity: 0;
	visibility: hidden;
	@include tr(.2s);
	&.is-active {
		opacity: 1;
		visibility: visible; }
	&_thanks {
		@include vertical;
		padding: 0;
		background-color: transparent;
		pointer-events: none;
		#{$P}__in {
			overflow: visible;
			position: relative;
			display: inline-block;
			vertical-align: middle;
			height: auto;
			padding: 0;
			background-color: #fff; }
		#{$P}__head {
			padding: 50px; }
		#{$P}__hand {
			top: -33px;
			right: -33px;
			pointer-events: auto; } }
	@include md {
		left: 50px;
		right: 50px;
		max-width: 100%; }
	@include sm {
	 top: 15px;
	 left: 15px;
	 right: 15px;
	 bottom: 15px; }
	&__in {
		height: 100%;
		padding: 0 0 200px;
		overflow: hidden;
		#{$P}_nobtn & {
			padding-bottom: 50px; }
		// +md
 }		// 	padding: 0 0 150px
	&__head {
		padding: 50px 128px 25px 50px;
		@include md {
			padding: 30px 30px 40px;
			padding-right: 84px; }
		@include sm {
			// padding: 15px 15px 30px
			padding: 15px 15px 20px;
			padding-right: 54px; } }
	&__body {
		height: 100%;
		padding: 0 50px 100px;
		overflow: auto;
		@include md {
			padding: 0 30px 75px; }
		@include sm {
			// padding: 0 15px 75px
			padding: 0 15px 25px; }
		&_large {
		  	padding: 0 50px 250px; } }
	&__title {
		@include textSB;
		font-size: 40px;
		line-height: 1.2;
		color: $dark;
		text-transform: uppercase;
		@include md {
			font-size: 28px; } }
	p {
		margin-bottom: 20px;
		font-size: 22px;
		line-height: 1.2;
		color: $dark;
		&:last-child {
			margin-bottom: 0; }
		@include md {
			font-size: 22px;
			margin-bottom: 25px; }
		span {
			@include textSB;
			font-size: 24px; } }
	input, textarea {
		width: 100%;
		padding: 13px 0 12px;
		border-bottom: 1px solid #a7b8c2;
		margin-bottom: 15px;
		@include text;
		font-size: 20px;
		color: $dark;
		@include tr(.2s);
		@include placeholder {
			@include textL;
			color: $dark;
			@include tr(.2s); }
		&:focus {
			border-color: $dark;
			@include placeholder {
				opacity: 0; } }
		&:last-child {
			margin-bottom: 0; }
		@include md {
			padding: 12px 0;
			margin-bottom: 10px;
			font-size: 22px; } }
	textarea {
		height: 150px;
		@include md {
			height: 125px; } }
	&__bl {
		margin-bottom: 40px;
		@include tr(.2s);
		&:last-child {
			margin-bottom: 0; }
		&.is-hidden {
			opacity: 0;
			visibility: hidden; }
		@include md {
			margin-bottom: 30px; } }
	&__btn {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 1;
		width: 100%;
		height: 110px;
		background-color: $dark;
		background: #000;
		@include textB;
		font-size: 30px;
		color: #fff;
		letter-spacing: .2em;
		text-transform: uppercase;
		@include tr(.2s);
		.no-touch &:hover {
			background-color: lighten($dark, 5%); }
		&.is-static {
			position: static; }
		@include md {
			height: 75px;
			font-size: 28px; } }
	&__hand {
		position: absolute;
		top: 60px;
		right: 55px;
		z-index: 1;
		font-size: 0;
		cursor: pointer;
		.icon {
			font-size: 28px;
			stroke-width: 2px;
			stroke: $grey;
			@include tr(.2s); }
		.no-touch &:hover .icon {
			stroke: $dark; }
		@include md {
			top: 35px;
			right: 30px;
			.icon {
				font-size: 24px; } }
		@include sm {
			top: 20px;
			right: 15px; } } }

.rating {
	@include clr;
	$starW: 64px;
	$starH: 60px;
	$lal: 60px;
	&__bar {
		float: left;
		margin-right: 45px;
		overflow: hidden;
		position: relative;
		width: $starW*5;
		height: $starH;
		background: $star repeat-x left center;
		background-size: contain;
		cursor: pointer;
		@include md {
			width: $starW*.6666*5;
			height: $starH*.6666; }
		@include sm {
			float: none;
			margin: 0 0 25px; } }
	&__bar-val {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 1;
		// width: 80%
		width: 0%;
		background: $starFill repeat-x left center;
		background-size: contain; }
	&__list {
		position: relative;
		min-height: $starH;
		margin-left: $starW*5+45;
		@include md {
			min-height: $starH*.6666;
			margin-left: $starW*.6666*5+25; }
		@include sm {
			margin: 0; } }
	&__txt {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		font-size: 30px;
		line-height: $starH;
		color: $dark;
		cursor: pointer;
		@include tr(.2s);
		opacity: 0;
		visibility: hidden;
		.no-touch &:hover {
			text-shadow: 0 1px 0 $dark; }
		&.is-active {
			opacity: 1;
			visibility: visible; }
		@include md {
			font-size: 26px;
			line-height: $starH*.6666; } } }

.overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 999;
	background-color: rgba(#1E2024, .8);
	opacity: 0;
	visibility: hidden;
	@include tr(.2s .1s ease-in-out);
	&.is-active {
		opacity: 1;
		visibility: visible;
		transition-delay: 0s; } }

.photo-drop {
	&__title {
		font-size: 24px;
		line-height: 1.2;
		color: $dark;
		margin-bottom: 10px;
		@include md {
			font-size: 22px; } }
	&__area {
		display: block;
		padding: 20px;
		border: 2px dashed #8c8c8c;
		cursor: pointer;
		text-align: center;
		input {
			display: none; } }
	&__ic {
		width: 100%;
		//height: 80px
		margin: 0 auto 15px;
		@include vertical;
		img {
			vertical-align: middle;
			max-width: 100%;
			max-width: 100%; } }
	&__txt {
		@include text;
		font-size: 24px;
		color: $dark;
		span {
			@include textSB; }
		@include md {
			font-size: 22px; } } }

.faq {
	$P: &;
	color: $dark;
	&__title {
		font-size: 30px;
		line-height: 1.2;
		margin-bottom: 15px;
		@include md {
			font-size: 24px; } }
	&__el {
		margin-bottom: 0; }
	&__name {
		padding-bottom: 20px;
		cursor: pointer;
		span {
			@include textM;
			line-height: 1.2;
			font-size: 30px;
			border-bottom: 2px solid $dark;
			@include tr(.2s); }
		#{$P}__el.is-active	& span {
			border-color: transparent; }
		@include md {
			padding-bottom: 15px;
			span {
				font-size: 22px;
				border-width: 1px; } } }
	&__answ {
		display: none;
		padding-bottom: 40px;
		@include md {
			padding-bottom: 25px; }
		p, ul, li {
			@include textM;
			line-height: 1.2;
			margin-bottom: 15px;
			&:last-child {
				margin-bottom: 0; } }
		li {
			font-size: 16px;
			@include md {
				font-size: 14px; } }
		p {
			font-size: 18px;
			@include md {
				font-size: 16px; } } } }
